// Стандартные импорты Rails
import Rails from "@rails/ujs"
import Turbolinks from "turbolinks"
import * as ActiveStorage from "@rails/activestorage"
import "channels"

// Подключение стилей и JS для Bootstrap и других библиотек
import '../js/bootstrap_js_files.js'
import '../packs/application.scss'
import "@fortawesome/fontawesome-free/css/all.css"
import "trix"
import "@rails/actiontext"
import "moment"
import "daterangepicker"

// Поддержка jQuery и Toastr (если они вам все еще нужны)
window.jQuery = $;
window.$ = $;
global.toastr = require("toastr")

// Запуск стандартных компонентов Rails
Rails.start()
Turbolinks.start()
ActiveStorage.start()


document.addEventListener('turbo:load', () => {
  const sections = document.querySelectorAll('.category-heading');
  const navLinks = document.querySelectorAll('.store-nav-link');
  const stickyHeader = document.getElementById('sticky-header-text');
  const defaultTitle = "iLab <span class='text-muted'><strong>Store</strong></span>";

  if (sections.length === 0 || !stickyHeader) return;

  const observer = new IntersectionObserver((entries) => {
    entries.forEach(entry => {
      const id = entry.target.getAttribute('id');
      const navLink = document.querySelector(`.store-nav-link[href="#${id}"]`);

      if (entry.isIntersecting && entry.intersectionRatio >= 0.5) {
        navLinks.forEach(link => link.classList.remove('active'));
        if (navLink) navLink.classList.add('active');
        stickyHeader.innerHTML = entry.target.textContent;
      }
    });

    const anyVisible = entries.some(entry => entry.isIntersecting);
    if (!anyVisible && window.scrollY < 200) {
      stickyHeader.innerHTML = defaultTitle;
    }
  }, { rootMargin: "-112px 0px 0px 0px", threshold: [0.5, 1] });

  sections.forEach(section => observer.observe(section));
});
